import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-dx-council.css"

export default function Home() {
  const description = `やさしいDX推進協議会では、中小IT事業者が協力し、アイデアと技術を持ち寄って、ITを活用した身近な“ものづくり”を通じて人々の生活がより良くなることを志しつつ、ともに収益力のアップを目指しています。`
  const pageUrl = `https://www.hotsoul.jp/dx-council/`
  const imageUrl = `https://www.hotsoul.jp/ogp_dx-council.jpg`

  return (
    <Layout>
      <Seo title="やさしいDX推進協議会" description={description} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">

        <section className="mv_dx">
          <div className="mv_dx-inner">
            <p className="txt1"><span>やさしいDX推進協議会</span></p>
            <p className="txt2 pc">
              中小IT事業者が協力し、アイデアと技術を持ち寄って、<br/>
              ITを活用した身近な“ものづくり”を通じて<br/>
              人々の生活がより良くなることを志しつつ、<br/>
              ともに収益力のアップを目指しています。
            </p>
          </div>
        </section>

        <section className="mv-lead sp">
          <div className="mv-lead-inner">
            <p className="txt2">
              中小IT事業者が協力し、アイデアと技術を持ち寄って、
              ITを活用した身近な“ものづくり”を通じて人々の生活がより良くなることを志しつつ、
              ともに収益力のアップを目指しています。
            </p>
          </div>
        </section>
          
        <section className="rinen">
          <h2>当会の理念</h2>
            <div className="rinen-inner">
              <p className="lead">
                　中小IT事業者が協力し、新たな製品・サービスの開発や埋もれている優れた製品・サービスの発掘を行い、
                それを各々の事業として育て収益力を強化することを支援する。
                それにより身近な場所での「易しく」「優しい」DXを推進し、ソフトウェアがもつ大きな可能性を発揮して、
                希望にあふれた未来の実現に寄与する。
              </p>
              <h3>
                <span className="bg-yellow">大切にする言葉</span>
              </h3>
              <div className="kotoba">
                <StaticImage
                  className="ph1" 
                  src="../../images/main-dx-council/item1.png"
                  alt="大切にする言葉"
                  placeholder="blurred"
                />
                <ul>
                  <li><span className="bg-yellow">「ありがとうございます」</span></li>
                  <li><span className="bg-yellow">「ごめんなさい」</span></li>
                  <li><span className="bg-yellow">「これでよろしいですか？」</span></li>
                  <li><span className="bg-yellow">「どうぞよろしくお願いいたします」</span></li>
                </ul>
              </div>
              <p className="att">
                （出典）「ありえないレベルで人を大切にしたら23年連続黒字になった仕組み」
                <br className="pc"/>
                ダイヤモンド社　近藤 宣之・株式会社日本レーザー代表取締役（2021年1月現在&nbsp;代表取締役会長）
              </p>
            </div>
        </section>

        <section className="activity">
          <h2>活動報告</h2>
          <div className="activity-bg">
            <div className="activity-inner">
              <div className="flex">
              <div className="flex-item item1">
                  <figure>
                    <StaticImage
                      src="../../images/main-dx-council/ph3.jpg"
                      alt="活動報告"
                      placeholder="blurred"
                    />
                  </figure>
                  <p className="date">
                    2021.9.5
                  </p>
                  <p className="title">
                    【農産物直売所マップ】<br/>
                    低コスト配送の実験をしました
                  </p>
                  <p className="txt">
                    もし配送料が格安だったら、ちょっと遠方でもお客さんは付くのだろうか？
                  </p>
                  <div className="dx-btn">
                    <a href="#">準備中です</a>
                  </div>
                </div>
                <div className="flex-item item2">
                  <figure>
                    <StaticImage
                      src="../../images/main-dx-council/ph2.jpg"
                      alt="活動報告"
                      placeholder="blurred"
                    />
                  </figure>
                  <p className="date">
                    2021.8.20
                  </p>
                  <p className="title">
                    【マトリクスプロジェクト】<br/>
                    プロトタイプ開発に着手します！
                  </p>
                  <p className="txt">
                    特許出願などの準備を進め、いよいよ最初のプロトタイプ開発に着手します。
                  </p>
                  <div className="dx-btn">
                    <a href="#">準備中です</a>
                  </div>
                </div>
                <div className="flex-item item3">
                  <figure>
                    <StaticImage
                      src="../../images/main-dx-council/ph1.jpg"
                      alt="活動報告"
                      placeholder="blurred"
                    />
                  </figure>
                  <p className="date">
                    2021.7.9
                  </p>
                  <p className="title">
                    【定例会】<br/>
                    第21回 定例会
                  </p>
                  <p className="txt">
                    ハマポートにて初のリアル開催！をしました。
                  </p>
                  <div className="dx-btn">
                    <Link to={`first-real-meeting`}>記事を読む</Link>
                  </div>
                </div>
              </div>
              <p className="archive"><Link to={`articles`}>他の記事も読む</Link></p>
            </div>
          </div>
        </section>

        <section className="voice">
          <h2>参加メンバーの声</h2>
          <StaticImage
            src="../../images/main-dx-council/ph4.png"
            alt="金井 克治さん"
            placeholder="blurred"
          />
          <div className="voice-inner">
            <h3><span className="bg-yellow">高齢者のお役に立つ、“Zoomの3D版”の製品化は私の使命</span></h3>
            <p className="company-name">大手IT企業勤務</p>
            <p className="name">金井 克治<span>さん</span></p>
            <p>
              大手IT企業に勤務し金融機関向けのソリューション展開をになっています。
              同時に、BNIや倫理法人会といった交流会でも重要なお役目を頂いていて、ソフトウェア開発会社と教育研修会社の顧問もやっています。
              <br/>
              「やさしいDX推進協議会」では、主に“3Dマトリクスプロジェクト”を企画開発準備中です。
              これは、高齢者でも簡単に使える、Zoomの3D版のようなものです。
              <br/>
              母を亡くし、父が認知症になった知人から「リアルタイムで通信できる、いいツールはないですか？」と相談を受けたことが、きっかけでした。
              コロナ禍でなかなか会いに行けない時でも、お互いに目の前にいるような感覚になるので、認知症の予防にも役立つと信じています。
              高齢者をはじめ、世の中のためになるこの製品化は、自分のミッションだと捉えています。
            </p>
          </div>
          <StaticImage
            src="../../images/main-dx-council/ph5.png"
            alt="布施 健太郎さん"
            placeholder="blurred"
          />
          <div className="voice-inner">
            <h3><span className="bg-yellow">豊富な人脈を紹介してくれる、居心地のいい場所</span></h3>
            <p className="company-name">株式会社On And On</p>
            <p className="name">布施 健太郎<span>さん</span></p>
            <p>
              株式会社On And Onを経営していて、中小企業のデジタル化やIT導入の支援をしています。
              また、法人向けのIT技術者教育や、AI技術者の育成などにも力を入れています。
              会社も自宅も厚木にあります。妻の実家が近所で農業をやっていることもあり、
              「やさしいDX推進協議会」では“農産物直売所のネットワーク化”に関わっていきたいと思っています。
              <br/>
               参加者の方は、皆さん顔が広くて、知り合いが増えました。
               意見を伝えると、「あの人と合うかもね！」と紹介いただけるのでありがたいです。
               また、知識も豊富でモチベーションも高いので、勉強になります。なにより楽しみながら参加しているので、
               自分にとっては居心地の良い空間ですね。
               今後は自分が中心となったプロジェクトを動かして、周りの人を巻き込んでいきたいです。
            </p>
          </div>
          <StaticImage
            src="../../images/main-dx-council/ph6.png"
            alt="加藤 悟史さん"
            placeholder="blurred"
          />
          <div className="voice-inner">
            <h3><span className="bg-yellow">ビジネスにつなげるアイデアで、日本のIT化を推進</span></h3>
            <p className="company-name">株式会社ウッドペッカー</p>
            <p className="name">加藤 悟史<span>さん</span></p>
            <p>
              株式会社ウッドペッカーで取締役をしています。仕事内容はシステム開発がメインです。
              中小企業向けに業務をIT化して改善するご提案と、大手企業向けに弊社の技術者を参画させてプロジェクトのお手伝いをしています。
               <br/>
              「やさしいDX推進協議会」は皆さんアイデアマンなので、いつも刺激を受けています。
              視野が広がり、新しい発想やヒントを得られるのが良いですね。
              このアイデアがどうしたらビジネスにつながるか、を考えるのも面白いです。
              <br/>
              日本では、ITはまだ浸透していないと感じます。最先端なのはわかるけど、うちの会社のはまだ早いとおっしゃる経営者の方が多いです。
              “ITは便利だけど、どう利用していいかわからない”といった方にもアプローチしていきたいです。
            </p>
          </div>
        </section>

        <section className="offer">
          <h2>やさしいDX推進協議会 <br className="sp"/>参加者募集中！</h2>
          <StaticImage
            src="../../images/main-dx-council/ph7.png"
            alt="やさしいDX推進協議会参加者募集中！"
            placeholder="blurred"
          />
          <div className="offer-bg">
            <div className="offer-inner">
              <p>
                やさしいDX推進協議会は、来るものは拒まず去る者は追わずの、なんとなく気の合う仲間が広場に集まって話し込んでいるような、
                とっても自由な雰囲気の会です。会費や面倒な入退会手続きはありません。
                面白そうだなと思った中小IT事業者、エンジニア・プログラマーの皆様、ぜひ一度、お気軽にご参加ください。
                <br/>
                また「これ、ITで何とかできないの？」といったお困りごとを抱えていらっしゃる方も、
                ぜひ一度お話をお聞かせいただければと思います。
                <br/>
                何かのお役に立てるかも？！しれません。
              </p>
              <div className="teireikai">
                <StaticImage
                  className="ph2"
                  src="../../images/main-dx-council/item2.png"
                  alt="定例会"
                  placeholder="blurred"
                />
                <h3><span className="bg-yellow">定例会</span></h3>
                <div className="flex parent">
                  <div className="flex child">
                    <div className="flex-item left">日時</div>
                    <div className="flex-item right">隔週金曜日<br/>午前 6:30～7:15</div>
                  </div>
                  <div className="flex child">
                    <div className="flex-item left">場所</div>
                    <div className="flex-item right">原則<br/>オンライン開催</div>
                  </div>
                  <div className="flex child">
                    <div className="flex-item left">会費</div>
                    <div className="flex-item right">無料</div>
                  </div>
                </div>
              </div>
              <div className="main-dx-btn">
                <Link to={`/contact-us`}>お問い合わせ</Link>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}
